<script>
export default {
    data() {
        return {
            isLoggedIn: false
        };
    },
    mounted() {
        if (!window.Kakao.isInitialized()) {
            window.Kakao.init('3026b75923ebe4d6d23187fa55dcdfa5');
        }
    },
    methods: {
        kakaoLogin() {
            window.Kakao.Auth.login({
                scope: 'profile_nickname, profile_image',
                success: this.getKakaoAccount
            });
        },
        getKakaoAccount() {
            window.Kakao.API.request({
                url: '/v2/user/me',
                success: (res) => {
                    const kakao_account = res.kakao_account;
                    const profile = kakao_account.profile.profile_image_url; // ✅ 수정됨
                    const nickname = kakao_account.profile.nickname; // ✅ 수정됨

                    console.log('프로필 이미지 URL:', profile);
                    console.log('닉네임:', nickname);

                    this.isLoggedIn = true;
                    alert(`${nickname}님 로그인 성공!`);
                },
                fail: (error) => {
                    alert('카카오 로그인 실패');
                    console.log(error);
                }
            });
        },
        kakaoLogout() {
            if (!window.Kakao.Auth.getAccessToken()) {
                console.log('이미 로그아웃 상태입니다.');
                return;
            }

            window.Kakao.Auth.logout((response) => {
                console.log('log out', response);
                this.isLoggedIn = false;
                window.Kakao.Auth.setAccessToken(null); // ✅ 권장
            });
        }
    }
};
</script>
